<template>
  <div class="login">
    <div class="login-content">
      <span class="login-title">随心随易商城运营管理</span>
      <div class="login-input-content">
        <div :class="['login-commen', 'input-container',{'focus':isUserNameFocus}]">
          <span class="iconfont icon-yonghu icon" />
          <input
            class="input"
            type="text"
            @focus="isUserNameFocus = true"
            @blur="isUserNameFocus = false"
            v-model="userName"
            placeholder="请输入用户名"
          >
        </div>
        <div :class="['login-commen', 'input-container',{'focus':isPwdFocus}]">
          <span class="iconfont icon-mima icon" />
          <input
            class="input"
            type="password"
            @focus="isPwdFocus = true"
            @blur="isPwdFocus = false"
            v-model="pwd"
            placeholder="请输入密码"
          >
        </div>
        <div
          class="login-commen logined"
          @click="login"
        >
          登陆
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import { encryptDes } from "@/commen/js/des.js";
// import md5  from "js-md5";

export default {
    data() {
        return {
            isUserNameFocus:false,
            isPwdFocus:false,
            userName:"",
            pwd:""
        };
    },
    created() {
        this.enterEvent();
    },
    methods: {
        // 按键登陆
        enterEvent (){
            const _this = this;
            document.onkeydown = function() {
                let key = window.event.keyCode;
                if (key === 13) {
                    _this.login();
                }
            };

        },
        login (){
            if(!this.userName){
                this.$message({
                    message:"用户名不能为空",
                    type:"warning"
                });
                return;
            }
            if(!this.pwd){
                this.$message({
                    message:"密码不能为空",
                    type:"warning"
                });
                return;
            }

            let userName = this.userName.trim(),
                pwd = this.pwd.trim();
            // 加密key
            // const key = "suixin";

            this.axios.post("/admin/dologin",{
                "user_name":userName,
                // "user_name":encryptDes(userName,key),
                "password":pwd
                // "password":md5(pwd)
            }).then(res => {
                localStorage.setItem("token",res.data.token);
                this.$router.replace({
                    name:"index"
                });
                return;
            }).catch(error => {
                this.$message.error({
                  message:error
                });
            });


        }
    },
    components: {},
    destroyed() {
        document.onkeydown = null;
    },
};
</script>

<style lang="scss" scoped>
.login {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url("../../static/images/bg.png") no-repeat 100% 100% /cover;
  .login-content {
    height: 450px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    .login-title {
      color: white;
      font-size: 40px;
      font-weight: bold;
      letter-spacing: 20px;
      text-shadow: 0 3px 0 black;
    }
    .login-input-content {
      width: 500px;
      height: 350px;
      box-sizing: border-box;
      padding-top: 20px;
      box-shadow: 0 0 10px 0 rgb(160, 160, 160) inset;
      display: flex;
      flex-direction: column;
      align-items: center;
      .input-container{
        margin-top: 30px;
        box-sizing: border-box;
        padding: 0 15px;
        display: flex;
        align-items: center;
        border: 1px solid  rgba(247, 150, 28, 0.5);
        transition: all 0.2s linear;
        .icon{
          font-size: 20px;
          color: white;
          margin-right: 10px;
        }
        .input{
          width: 300px;
          height: 25px;
          color: white;
          font-size: 18px;
          background: transparent;
          border: none;
          &:focus{
            outline: none;
          }
        }
      }
      .focus{
         border: 1px solid  rgba(247, 150, 28, 1);
      }
      .login-commen {
        width: 400px;
        height: 50px;
        border-radius: 4px;
      }
      .logined {
        display: grid;
        place-items: center;
        color: white;
        font-weight: bold;
        letter-spacing: 10px;
        margin-top: 55px;
        background: #f7961c;
        user-select: none;
        cursor: pointer;
      }
    }
  }
}
</style>
